<template>
  <div>
      <v-row>
          <v-col cols="12" md="3" lg="3" xl="3">
          <v-card>
              <v-card-title style="background-color:#B2DFDB">Input Here</v-card-title>
                  <v-card-text>
                      <br/>
                      <v-form ref="saveData" @submit.prevent="saveData">
                          <v-text-field 
                              label="ID Number"
                              dense
                              outlined
                              v-model="form.idNumber"
                              :rules="rules.required"
                              :value="capitalizedForm.idNumber"
                          />
                          <label><b>Full Name</b></label>
                          <v-text-field 
                              label="Last Name"
                              dense
                              outlined
                              v-model="form.lastName"
                              :rules="rules.required"
                              :value="capitalizedForm.lastName"
                          />
                          <v-text-field 
                              label="First Name"
                              dense
                              outlined
                              v-model="form.firstName"
                              :rules="rules.required"
                              :value="capitalizedForm.firstName"
                          />
                          <v-text-field 
                              label="Middle Name"
                              dense
                              outlined
                              v-model="form.middleName"
                              :rules="rules.required"
                              :value="capitalizedForm.middleName"
                          />
                          <v-autocomplete
                            label="Position"
                            :items="positionItems"
                            v-model="form.position"
                            dense
                            outlined
                            :rules="rules.required"
                          />
                          <v-btn type="submit" color="success" block>SAVE</v-btn>
                          <v-btn @click="printPdf" color="warning" block :disabled="tableData.length == 0?true:false">PRINT</v-btn>
                      </v-form>
                          
                  </v-card-text>
          </v-card>
      </v-col>
      <v-col cols="12" md="9" lg="9" xl="9">
          <v-card>
              <v-card-title style="background-color:#B2DFDB">Summary</v-card-title>
                  <v-card-text>
                      <br/>
                          <v-simple-table
                              fixed-header
                              height="60vh"
                          >
                              <thead>
                                  <tr>
                                      <th>ID No.</th>
                                      <th>Last Name</th>
                                      <th>First Name</th>
                                      <th>Middle Name</th>
                                      <th>Position</th>
                                  </tr>
                              </thead>
                              <tbody>
                                  <tr v-for="(item,i) in tableData" :key="i">
                                      <td>{{ item.idNumber }}</td>
                                      <td>{{ item.lastName }}</td>
                                      <td>{{ item.firstName }}</td>
                                      <td>{{ item.middleName }}</td>
                                      <td>{{ item.position }}</td>
                                      <td></td>
                                  </tr>
                              </tbody>

                          </v-simple-table>
                  </v-card-text>
          </v-card>
      </v-col>
      </v-row>
      
      
  </div>
  
</template>
<script>
// import jsPDF from 'jspdf';
// import 'jspdf-autotable';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
export default {
  data:()=>({
      tableData:[],
      form:{
          idNumber:'',
          lastName:'',
          firstName:'',
          middleName:'',
          position:''
      },
      positionItems:['Manager','Supervisor','Team Leader','Member'],
      rules:{
          required: [
              v => !!v || 'Field is required'
          ],
      },
  }),
  methods:{
      saveData(){
          if(this.$refs.saveData.validate()){
              this.tableData.push(this.capitalizedForm)
              this.form = {}
              this.$refs.saveData.resetValidation();
          }
          
          // console.log(this.tableData)
     
      },
      capitilizedFirst(str){
          if(str){
              return str.charAt(0).toUpperCase() + str.slice(1).toLowerCase()
          }else{
              return '';
          }
         
      },
      printPdf() {
        const tableBody = []
        const tableHeader = ['ID', 'Lastname', 'Firstname', 'MiddleName', 'Position'];
        

        // Calculate the vertical position for the stack
        

        this.tableData.forEach(row=>{
            const rowData = [
               row.idNumber.toString(),
                row.lastName,
                row.firstName,
                row.middleName,
                row.position
            ]
            tableBody.push(rowData)
        })
        const tableHeight = tableBody.length == 0?2 * 20:tableBody.length * 20; // Assuming each row has a height of 20 (adjust as needed)
        const stackY = tableBody.length == 0?250:550 - tableHeight; // Adjust the i
        const docDefinition = {
        content: [
            { text: 'This is demo only', alignment: 'center' , fontSize:'25',margin: [0, 0, 0, 10]},
            {
                table: {
                    headerRows: 1,
                    widths: ['*', '*', '*', '*','*'], // Set column widths
                    body: [
                    tableHeader.map((headerCell) => ({
                        text: headerCell,
                        fillColor: '#B2DFDB', // Background color for the header cells
                        style: 'tableHeader', // Apply a style for the header cells if needed
                    })),
                    ...tableBody
                    ],
                },
                // layout: {
                //     fillColor: '#B2DFDB', // Background color for the header
                // },
            },
            {
                absolutePosition: { x: 300, y: stackY }, // Adjust the coordinates as needed
                margin: [0, 10, 0, 0],
                stack: [
                    { text: 'name', alignment: 'left', fontSize: '12',margin: [70, 0, 0, -10] },
                    { canvas: [{ type: 'line', x1: 20, y1: 10, x2: 180, y2: 10}] },
                    { text: 'signature', alignment: 'left', fontSize: '12', margin: [70, 0, 0, 0] },
                ],
             
            },

        ],
        };

        pdfMake.createPdf(docDefinition).download('output.pdf');
    }
  },
  computed:{
      capitalizedForm(){
          return{
              idNumber: this.form.idNumber,
              firstName: this.capitilizedFirst(this.form.firstName),
              lastName: this.capitilizedFirst(this.form.lastName),
              middleName: this.capitilizedFirst(this.form.middleName),
              position: this.form.position
          }
      }
  },
  watch:{
     
  }
}
</script>